@mixin progressiveRules {
	progress::after {
		content: '';
		position: absolute;
		top: -1px;
		right: 0;
		height: 3px;
		width: 3px;
		background-color: #E78B48;
	}

	progress::-webkit-progress-bar {
		position: relative;
		height: 1px;
		background-color: #A5A5A5;
	}

	progress::-webkit-progress-value {
		position: absolute;
		top: -1px;
		height: 3px;
		background-color: #E78B48;
		transition: width 3s;
	}

	progress::-moz-progress-bar {
		background: #E78B48 !important;
		border: none !important;
		height: 3px !important;
		margin-top: -1px;
	}

	progress::-ms-fill {
		background: #E78B48 !important;
		color: #A5A5A5 !important;
		height: 3px !important;
	}
}

@mixin blockTitle($top, $left, $bottom, $width, $wrap) {
	display: inline-block;
	margin-left: $left;
	margin-top: $top;
	height: 58px;
	width: $width;
	color: #333;
	font-family: $desk-bold-raleway;
	font-size: 48px;
	font-weight: 700;
	white-space: $wrap;
	line-height: 58px;
	margin-bottom: $bottom;
	letter-spacing: 0.6px;
	position: relative;
}

