//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.requisites {
		display: inline-block;
		position: relative;
		width: 100%;
		height: 760px;
	}

	.requisites_title {
		@include blockTitleMob2(0px, 30px, 0px, 270px, nowrap);

		position: unset;
	}

	.requisites_title::before {
		content: '';
		position: absolute;
		left: 0px;
		top: 16px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.urkanian_currency_account_mob {
		position: absolute;
		display: block;
		text-align: center;
		top: 80px;
		left: 15px;
		height: 85px;
		width: 290px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 24px 0 rgba(209, 209, 209, 0.15);
		cursor: pointer;

		h2 {
			display: inline-block;
			position: relative;
			margin-top: 20px;
			margin-left: 0px;
			height: 26px;
			width: 100%;
			color: #333333;
			font-family: $desk-bold-raleway;
			font-size: 16px;
			line-height: 24px;
			white-space: unset;
		}
	}

	.foreign_currency_account_mob {
		position: absolute;
		text-align: center;
		left: 15px;
		top: 165px;
		height: 85px;
		width: 290px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 24px 0 rgba(209, 209, 209, 0.15);
		cursor: pointer;

		h2 {
			display: inline-block;
			position: relative;
			margin-top: 20px;
			margin-left: 0px;
			height: 26px;
			width: 100%;
			color: #333333;
			font-family: $desk-bold-raleway;
			font-size: 16px;
			line-height: 24px;
			white-space: unset;
		}
	}

	.urkanian_currency_account_details_mob {
		position: absolute;
		display: block;
		top: 250px;
		height: 550px;
		width: 290px;
		left: 15px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 10px 0 rgba(209, 209, 209, 0.2);

		.raiffeisen_details_mob,
		.privat_details_mob {
			display: block;
			margin-left: 20px;
			margin-top: 20px;
			margin-bottom: 0;
			font-family: $desk-bold-raleway;
			font-size: 16px;
		}

		.raiffeisen_details_info_mob,
		.privat_details_info_mob {
			white-space: pre-line;
			display: inline-block;
			margin-left: 20px;
			margin-top: 5px;
			height: 224px;
			width: 250px;
			color: #333333;
			font-family: $desk-regular-raleway;
			font-size: 14px;
			line-height: 24px;

			strong {
				font-family: $desk-light-proxima;
				font-weight: unset;
				font-weight: 400;
			}
		}
	}

	.privat_details_mob {
		position: absolute;
		top: 280px;
		left: 0px;
	}

	.raiffeisen_details_info_mob {
		margin-right: 0px;
		margin-bottom: 40px;
	}

	.disabled_currency {
		opacity: 0.35;
	}

	.foreign_currency_account_details_mob {
		display: none;
		position: relative;
		margin-top: 216px;
		height: 950px;
		width: 290px;
		margin-left: 15px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 10px 0 rgba(209, 209, 209, 0.2);

		.euro_cuurency_details_mob,
		.usd_currency_details_mob {
			display: inline-block;
			text-align: center;
			margin-left: 0px;
			margin-top: 20px;
			margin-bottom: 0;
			font-family: $desk-bold-raleway;
			font-size: 16px;
		}

		.euro_currency_details_info_mob,
		.usd_currency_details_info_mob {
			white-space: pre-line;
			display: inline-block;
			margin-left: 20px;
			margin-top: 5px;
			height: 224px;
			width: 250px;
			color: #333333;
			font-family: $desk-regular-raleway;
			font-size: 12px;
			line-height: 24px;

			strong {
				font-family: $desk-light-proxima;
				font-weight: unset;
				font-weight: 400;
			}
		}
	}

	.usd_currency_details_mob {
		position: absolute;
		top: 460px;
		left: 0px;
		margin: unset;
	}

	.usd_currency_details_info_mob {
		position: absolute;
		top: 520px;
		width: 250px !important;
		left: 0px;
		margin: unset;
	}

	.display_block_mob {
		display: block !important;
	}

	.display_block_none {
		display: none !important;
	}
}

