//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	#googleMap {
		position: relative;
		width: 100%;
		height: 300px;
		border-top: 2px solid $arrowcolor;
		margin-top: -20px;
		margin-bottom: 35px;
		z-index: 100;
	}

	.our_contacts_details {
		width: 100%;
		height: 640px;
		display: inline-block;
		position: unset;
		background: inherit;
		box-shadow: unset;
		font-family: $desk-regular-raleway;
		font-size: 14px;
		line-height: 24px;

		strong {
			font-family: $desk-light-proxima;
			font-weight: 400;
		}
	}

	.our_contacts_details_title {
		@include blockTitleMob2(0px, 35px, 0px, 270px, nowrap);

		position: unset;
	}

	.our_contacts_details_title::before {
		content: '';
		position: absolute;
		left: 0px;
		top: 411px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.our_contacts_details h3,
	.our_contacts_details span {
		margin-left: 35px;
		display: inline-block;
	}

	.legal_address {
		font-family: $desk-bold-proxima;
		margin-bottom: 0px;
	}

	.legal_address_info {
		width: 290px;
		font-size: 16px;
		font-family: $desk-regular-raleway;
		margin-top: 5px;
	}

	.actual_address {
		font-family: $desk-bold-proxima;
		margin-top: 0px;
		margin-bottom: 0px;
		white-space: pre-line;

		br {
			display: none;
		}
	}

	.actual_adress_info {
		width: 200px;
		font-size: 16px;
		font-family: $desk-regular-raleway;
		margin-top: 5px;
	}

	.actual_adress_info.email {
		width: 250px;
	}

	.contact_person {
		font-family: $desk-bold-proxima;
		margin-bottom: 0px;
		margin-top: 0px;
		white-space: pre-line;
	}

	.contact_person_info {
		width: 250px;
		font-size: 16px;
		font-family: $desk-regular-raleway;
		margin-top: 5px;
	}

	.lviv_address {
		font-family: $desk-bold-proxima;
		width: 290px;
		margin-bottom: 0px;
		margin-top: 30px;
		white-space: normal;
	}

	.lviv_address_info {
		width: 250px;
		font-size: 16px;
		font-family: $desk-regular-raleway;
		white-space: pre-line;
		margin-top: -17px;
	}

	.dacha_center_location,
	.gm-style-iw {
		height: 113px;
		width: 225px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

		.dacha_center_location_title {
			display: inline-block;
			margin-top: 31px;
			margin-left: 21px;
			margin-bottom: 0;
			height: 24px;
			width: 149px;
			color: #333333;
			font-family: $desk-bold-raleway;
			font-size: 20px;
			font-weight: bold;
			line-height: 24px;
		}

		.dacha_center_location_info {
			display: inline-block;
			margin-top: 12px;
			margin-left: 22px;
			height: 19px;
			width: 176px;
			color: #333333;
			font-family: $desk-regular-raleway;
			font-size: 14px;
			line-height: 19px;
		}

		i {
			font-style: unset;
			font-weight: 400;
			font-family: $desk-light-proxima;
		}
	}

	.gm-style-iw div {
		overflow: hidden !important;
		width: 100% !important;
	}
}

