@import "partials/page_header";
@import "partials/page_footer";
@import "partials/header_top_sms_pop_up";
@import "partials/mixins";
@import "partials/about_us_partials/zaporuka_projects_help_form";
@import "partials/about_us_partials/about_us_help_section";
@import "partials/contacts_partials/google_maps";
@import "partials/contacts_partials/social_contacts_mobile";
@import "partials/contacts_partials/requisites_mobile";

.home_header_bottom_nav li a sup {
	top: -5.5px !important;
}

#googleMap {
	position: relative;
	width: 100%;
	height: 722px;
	border-top: 2px solid $arrowcolor;
	margin-top: 61px;
	margin-bottom: 0;
}

.our_contacts_details {
	width: 512px;
	height: 720px;
	position: absolute;
	top: 125px;
	left: 0px;
	background: rgb(247, 247, 247);
	box-shadow: 0px 30px 30px 20px rgba(247, 247, 247, 0.9);
	font-family: $desk-regular-raleway;
	font-size: 14px;
	line-height: 24px;

	strong {
		font-family: $desk-light-proxima;
		font-weight: 400;
	}
}

.our_contacts_details_title {
	@include blockTitle(60px, 115px, 25px, 273px, nowrap);
}

.our_contacts_details_title::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 27px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.our_contacts_details h3,
.our_contacts_details span {
	margin-left: 113px;
	display: inline-block;
}

.legal_address {
	font-family: $desk-bold-proxima;
	margin-bottom: 0px;
}

.legal_address_info {
	width: 330px;
	font-size: 16px;
	font-family: $desk-regular-raleway;
	margin-top: 5px;
}

.actual_address {
	font-family: $desk-bold-proxima;
	margin-top: 28px;
	margin-bottom: 0px;
}

.actual_adress_info {
	width: 200px;
	font-size: 16px;
	font-family: $desk-regular-raleway;
	margin-top: 5px;
}

.actual_adress_info.email {
	width: 250px;
}

.contact_person {
	font-family: $desk-bold-proxima;
	margin-bottom: 0px;
	margin-top: 10px;
	white-space: pre-line;
}

.contact_person_info {
	width: 250px;
	font-size: 16px;
	font-family: $desk-regular-raleway;
	margin-top: 5px;
}

.lviv_address {
	font-family: $desk-bold-proxima;
	width: 390px;
	margin-bottom: 0px;
	margin-top: 40px;
	white-space: nowrap;
}

.lviv_address_info {
	width: 250px;
	font-size: 16px;
	font-family: $desk-regular-raleway;
	white-space: pre-line;
	margin-top: -17px;
}

.dacha_center_location,
.gm-style-iw {
	height: 113px;
	width: 225px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

	.dacha_center_location_title {
		display: inline-block;
		margin-top: 31px;
		margin-left: 31px;
		margin-bottom: 0;
		height: 24px;
		width: 149px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		font-weight: bold;
		line-height: 24px;
	}

	.dacha_center_location_info {
		display: inline-block;
		margin-top: 12px;
		margin-left: 32px;
		height: 19px;
		width: 176px;
		color: #333333;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 19px;
	}

	i {
		font-style: normal !important;
		font-weight: 400;
		font-family: $desk-light-proxima;
	}
}

// Disable google logo and copyright
.gm-style-cc {
	display: none !important;
}

.gm-style a[href^="https://maps.google.com/maps"] {
	display: none !important;
}

.social_contacts {
	position: relative;
	height: 300px;
	width: 100%;
	margin-top: 18px;
}

.social_contacts_title {
	@include blockTitle(100px, 115px, 0px, 273px, nowrap);

	margin-right: 173px;
}

.social_contacts_title::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 27px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.social_contacts_title_help {
	@include blockTitle(100px, 115px, 0px, 273px, nowrap);
}

.social_contacts_media {
	display: inline-block;
	list-style-type: none;
	width: 600px !important;
	margin-left: 75px;
	margin-top: 71px;

	li {
		width: 120px;
		cursor: pointer;
		margin-right: 34px;
		display: inline-block;

		i {
			color: #BFBFBF;
			font-size: 15px;
		}

		span {
			height: 19px;
			width: 74px;
			color: #333333;
			margin-left: 10px;
			font-family: 'Raleway-Regular', sans-serif;
			font-size: 16px;
			line-height: 19px;
		}
	}
}

.social_contacts_media li:nth-of-type(2) {
	margin-right: 41px;
}

.social_contacts_media li a {
	text-decoration: none;
	color: inherit;
}

.social_contacts_media li:hover,
.social_contacts_media li:focus {
	i,
	span {
		color: #E78B48 !important;
	}
}

.social_contacts_help_button {
	position: absolute;
	left: 681px;
	top: 230px;
	height: 16px;
	width: 179px;
	color: #E78B48;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	text-decoration: none;
	white-space: nowrap;

	&:hover,
	&:focus {
		left: 687px;
		transition: .4s;
		opacity: 0.7;
	}
}

.social_contacts_help_button::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 192px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.requisites {
	display: inline-block;
	position: relative;
	width: 100%;
	height: 630px;
}

.requisites_title {
	@include blockTitle(70px, 115px, 0px, 273px, nowrap);
}

.requisites_title::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 27px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.urkanian_currency_account {
	position: absolute;
	top: 188px;
	left: 113px;
	height: 85px;
	width: 570px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 24px 0 rgba(209, 209, 209, 0.15);
	cursor: pointer;

	h2 {
		display: inline-block;
		position: relative;
		margin-top: 29px;
		margin-left: 91px;
		height: 26px;
		width: 391px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		line-height: 26px;
		white-space: nowrap;
	}
}

.foreign_currency_account {
	position: absolute;
	left: 683px;
	top: 188px;
	height: 85px;
	width: 570px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 24px 0 rgba(209, 209, 209, 0.15);
	cursor: pointer;
	z-index: 5;

	h2 {
		display: inline-block;
		position: relative;
		margin-top: 29px;
		margin-left: 71px;
		height: 26px;
		width: 391px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		line-height: 26px;
		white-space: nowrap;
	}
}

.urkanian_currency_account_details {
	position: absolute;
	top: 273px;
	left: 113px;
	height: 327px;
	width: 1140px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 10px 0 rgba(209, 209, 209, 0.2);

	.raiffeisen_details,
	.privat_details {
		display: block;
		margin-left: 88px;
		margin-top: 46px;
		margin-bottom: 0;
		font-family: $desk-bold-raleway;
		font-size: 16px;
	}

	.raiffeisen_details_info,
	.privat_details_info {
		white-space: pre-line;
		display: inline-block;
		margin-left: 88px;
		margin-top: 5px;
		height: 224px;
		width: 396px;
		color: #333333;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 28px;

		strong {
			font-family: $desk-light-proxima;
			font-weight: unset;
			font-weight: 400;
		}
	}
}

.privat_details {
	position: absolute;
	top: 0px;
	left: 560px;
	margin: unset;
}

.raiffeisen_details_info {
	margin-right: 73px;
}

.disabled_currency {
	opacity: 0.35;
}

.display_block {
	display: inline-block !important;
}

.foreign_currency_account_details {
	display: none;
	position: relative;
	margin-top: 143px;
	margin-left: 113px;
	height: 400px;
	width: 1140px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 10px 0 rgba(209, 209, 209, 0.2);

	.euro_cuurency_details,
	.usd_currency_details {
		display: block;
		margin-left: 88px;
		margin-top: 46px;
		margin-bottom: 0;
		font-family: $desk-bold-raleway;
		font-size: 16px;
	}

	.euro_currency_details_info,
	.usd_currency_details_info {
		white-space: pre-line;
		display: inline-block;
		margin-left: 88px;
		margin-top: 5px;
		height: 224px;
		width: 420px;
		color: #333333;
		font-family: $desk-regular-raleway;
		font-size: 14px;
		line-height: 24px;

		strong {
			font-family: $desk-light-proxima;
			font-weight: unset;
			font-weight: 400;
		}
	}
}

.usd_currency_details {
	position: absolute;
	top: 0px;
	left: 560px;
	margin: unset;
}

.usd_currency_details_info {
	position: absolute;
	top: 60px;
	width: 500px !important;
	left: 560px;
	margin: unset;
}

.urkanian_currency_account_mob,
.foreign_currency_account_mob,
.urkanian_currency_account_details_mob,
.raiffeisen_details_mob,
.privat_details_mob,
.raiffeisen_details_info_mob,
.privat_details_info_mob,
.foreign_currency_account_details_mob,
.euro_cuurency_details_mob,
.usd_currency_details_mob,
.euro_currency_details_info_mob,
.usd_currency_details_info_mob {
	display: none;
}

.about_us_help {
	margin-top: 90px !important;
}

//FOOTER EXCEPTIONS

.page_footer {
	margin-top: 101px !important;
}

// Footer MEDIA QUERY 320-1024

@media screen and (min-width: 320px) and (max-width: 1024px) {
	.page_footer {
		margin-top: 60px !important;
	}
}
