//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.social_contacts {
		position: relative;
		height: 350px;
		width: 100%;
		margin-top: 10px;
	}

	.social_contacts_title {
		@include blockTitleMob2(0px, 30px, 0px, 270px, nowrap);

		position: unset;
		margin-right: 0px;
	}

	.social_contacts_title::before {
		content: '';
		position: absolute;
		left: 0px;
		top: 16px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.social_contacts_title_help {
		@include blockTitleMob2(170px, 30px, 0px, 250px, unset);

		display: block !important;
	}

	.social_contacts_media {
		position: absolute;
		top: -10px;
		display: inline-block;
		list-style-type: none;
		width: 120px !important;
		left: 15px;
		top: 71px;
		margin: unset;

		li {
			width: 100%;
			cursor: pointer;
			margin-right: 0px;
			margin-bottom: 20px;
			display: inline-block;

			i {
				color: #BFBFBF;
				font-size: 15px;
			}

			span {
				height: 19px;
				width: 74px;
				color: #333333;
				margin-left: 10px;
				font-family: 'Raleway-Regular', sans-serif;
				font-size: 16px;
				line-height: 19px;
			}
		}
	}

	.social_contacts_media li:nth-of-type(2) {
		margin-right: 0px;
	}

	.social_contacts_media li a {
		text-decoration: none;
		color: inherit;
	}

	.social_contacts_media li:hover,
	.social_contacts_media li:focus {
		i,
		span {
			color: #E78B48 !important;
		}
	}

	.social_contacts_help_button {
		position: absolute;
		left: 30px;
		top: 300px;
		height: 16px;
		width: 179px;
		color: #E78B48;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		font-weight: bold;
		line-height: 16px;
		text-decoration: none;
		white-space: nowrap;

		&:hover,
		&:focus {
			left: 36px;
			transition: .4s;
			opacity: 0.7;
		}
	}

	.social_contacts_help_button::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 192px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}
}

